p {
  margin-bottom: 10px; }

a {
  text-decoration: none;
  cursor: pointer;
  color: #62a4ae;
  transition: color 200ms ease-in-out; }
  a:hover {
    color: #4b8992; }

em,
i {
  font-style: italic; }

strong,
b {
  font-weight: 700; }

.dashboard {
  font-family: 'Rubik', sans-serif;
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center; }
  .dashboard .pygrid-icon {
    margin-right: 5px;
    background-image: url("../../assets/img/pygrid.svg");
    height: 30px;
    width: 30px;
    background-repeat: no-repeat;
    background-size: cover;
    display: inline-block; }
  .dashboard .header {
    display: flex;
    color: white;
    justify-content: space-between;
    background: #222;
    padding: 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    box-shadow: #1A1A1A33 0px 5px 10px; }
    .dashboard .header .brand {
      flex-direction: row; }
    .dashboard .header .tabs {
      margin: 0; }
      .dashboard .header .tabs > li {
        border-radius: 2.5px;
        border: none;
        opacity: .5; }
      .dashboard .header .tabs .react-tabs__tab--selected {
        opacity: 1;
        background: #323232;
        color: white; }
        .dashboard .header .tabs .react-tabs__tab--selected:focus {
          box-shadow: none;
          outline: none; }
          .dashboard .header .tabs .react-tabs__tab--selected:focus::after {
            background: transparent; }
  .dashboard .tab {
    padding: 10px;
    margin: 0 20px; }
    .dashboard .tab .title {
      text-transform: none;
      font-weight: normal; }
    .dashboard .tab .table {
      box-shadow: #1A1A1A33 0px 5px 10px; }
  .dashboard .card {
    padding: 0 0 30px 0;
    height: 80vh;
    width: 80vw;
    border-radius: 10px; }
