.card {
  position: relative;
  background: #fff;
  padding: 30px;
  margin-bottom: 20px;
  border-radius: 5px;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.1);
  will-change: transform; }
  .card .header .subtitle {
    margin-top: 0px;
    color: #ccc;
    margin-bottom: 5px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-family: "Overpass", sans-serif;
    font-weight: 700; }
    @media (min-width: 640px) {
      .card .header .subtitle {
        margin-bottom: 10px; } }
  .card .header .heading {
    margin-top: 0px;
    color: #222; }
    @media (min-width: 640px) {
      .card .header .heading {
        font-size: 1.25rem; } }
    @media (min-width: 1440px) {
      .card .header .heading {
        font-size: 1.35rem; } }
  .card .content {
    color: #808080;
    font-size: 1.1rem;
    line-height: 2.25rem;
    max-height: 175px;
    overflow: hidden; }
    .card .content.no-links {
      max-height: 100%; }
  .card .links {
    padding-left: 0px;
    margin-bottom: 0px;
    list-style-type: none; }
    @media (min-width: 1024px) {
      .card .links {
        position: absolute;
        bottom: 30px;
        left: 30px;
        width: 100%; } }
    .card .links .content-link:last-of-type a {
      margin-bottom: 0px; }
    .card .links .content-link a {
      display: flex;
      align-items: center;
      margin-bottom: 10px; }
      .card .links .content-link a:hover .text {
        color: #222; }
      .card .links .content-link a .icon {
        background: #000;
        width: 40px;
        height: 40px;
        border-radius: 5px;
        margin-right: 10px; }
      .card .links .content-link a .text {
        color: #808080;
        font-size: 0.9rem;
        transition: color 200ms ease-in-out;
        text-transform: uppercase;
        letter-spacing: 2px;
        font-family: "Overpass", sans-serif;
        font-weight: 700; }
